@import "../../scss/mixins";

.navbar {
  background-color: aliceblue;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
 padding: 10px;
}

.navbar-img {
  width: 150px;
  @include media-surface {
    width: 100px;
    height: auto;
  }
}

.wrapper-navbar{
  display: flex;

}

.wrapper-navbar ul{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
   // margin-left: auto;

}

.Link{
    margin-right: 100px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    text-transform: uppercase;
    line-height: 27px;
    text-decoration: none;
    transition: all 0.6s;

    color: #551426;

    @include media-tablet{
      margin-right: 50px;
    }

    @include media-surface{
      margin-right: 15px;
      font-size: 16px;
    }

    &:hover {
      opacity: 0.6;
    }
}

.Link__active {
  opacity: 0.6;
  border-bottom: 1px solid #551426;
}